		<view class="text-area">
			<text class="title">{{title}}</text>
		</view>
		<button class="social-btn" @click="navigateToDouyin">ÂâçÂæÄÊäñÈü≥</button>
		<button class="scan-btn" @click="scanItem">Êâ´ÊèèÁâ©ÂìÅ</button>
		<view class="recipe-query-section">
			<input class="recipe-query-input" v-model="recipeQuery" placeholder="ËØ∑ËæìÂÖ•È£üÊùê" />
			<button class="recipe-query-btn" @click="recommendRecipe">Êé®ËçêÈ£üË∞±</button>
		</view>
		<button class="voice-btn" @click="startVoiceInput">ËØ≠Èü≥ÂΩïÂÖ•</button>
		
		<!-- Ê∑ªÂä†Êü•ËØ¢Â∫ìÂ≠òÂå∫Âüü -->
		<view class="query-section">
			<input class="query-input" v-model="queryItemName" placeholder="ËØ∑ËæìÂÖ•Ë¶ÅÊü•ËØ¢ÁöÑÁâ©ÂìÅÁßçÁ±ª" />
			<button class="query-btn" @click="queryInventory">Êü•ËØ¢</button>
		</view>

		<view class="inventory">
			<view class="inventory-item" v-for="(item, index) in inventory" :key="index">
				<image class="item-image" :src="item.imageUrl || '/static/default-image.png'" mode="aspectFill"></image>
				<text class="item-name">{{item.name}}</text>
				<text class="item-quantity">Êï∞ÈáèÔºö{{item.quantity}}</text>
				<text class="item-expire">‰øùË¥®ÊúüÔºö{{item.expireDate}}</text>
				<text class="item-date">Êâ´ÊèèÊó•ÊúüÔºö{{item.scanDate}}</text>
			</view>
		</view>

		<view class="incompatibility-section">
			<text class="section-title">Â∫ìÂ≠òÁâ©ÂìÅÁõ∏ÂÖã‰ø°ÊÅØ</text>
			<view class="incompatibility-item" v-for="(item, index) in incompatibilityList" :key="index">
				<text class="item-name">{{ item.name }}</text>
				<text class="incompatibility-info">{{ item.info }}</text>
			</view>
		</view>

		<view class="recipe-section">
			<text class="section-title">Êé®ËçêÈ£üË∞±</text>
			<view class="recipe-item" v-for="(recipe, index) in recipes" :key="index">
				<image class="recipe-image" :src="recipe.image" mode="aspectFill"></image>
				<text class="recipe-name">{{recipe.name}}</text>
				<text class="recipe-ingredients">ÊâÄÈúÄÈ£üÊùêÔºö{{recipe.ingredients.join('„ÄÅ')}}</text>
				<text class="recipe-steps">Âà∂‰ΩúÊ≠•È™§Ôºö{{recipe.steps}}</text>
				<text class="recipe-warning">Á¶ÅÂøå‰∫∫Áæ§Ôºö{{recipe.warning}}</text>
			</view>
		</view>

		<view class="manual-input-section">
			<button class="manual-input-btn" @click="navigateToManualInput">ÊâãÂä®ÂΩïÂÖ•</button>
		</view>

		<view class="manual-recipe-input-section">
			<button class="manual-recipe-input-btn" @click="navigateToManualRecipeInput">ÊâãÂä®ÂΩïÂÖ•ËèúË∞±</button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				title: 'Êô∫ËÉΩÂÜ∞ÁÆ±Â∫ìÂ≠ò',
				inventory: [],
				queryItemName: '',
				incompatibilityList: [], // Â≠òÂÇ®Áõ∏ÂÖã‰ø°ÊÅØ
				recipes: [], // Â≠òÂÇ®Êé®ËçêÁöÑÈ£üË∞±
				recipeQuery: ''
			}
		},
		onLoad() {
			this.getInventory();
		},
		methods: {
			navigateToDouyin() {
				window.location.href = 'https://www.douyin.com';
			},
			async submitData() {
				try {
					const res = await uniCloud.callFunction({
						name: 'submit-data',
						data: {
							name: 'ÁâõÂ•∂',
							quantity: 3,
							expireDate: '2023-12-31'
						}
					});
					if (res.result.code === 0) {
						uni.showToast({
							title: 'Êï∞ÊçÆÊèê‰∫§ÊàêÂäü',
							icon: 'success'
						});
					}
				} catch (error) {
					console.error('Êï∞ÊçÆÊèê‰∫§Â§±Ë¥•:', error);
				}
			},
			async getInventory() {
				try {
					const res = await uniCloud.callFunction({
						name: 'get-inventory'
					});

					if (res.result.code === 0) {
						this.inventory = res.result.data;
						this.getIncompatibilityInfo(); // Ëé∑ÂèñÁõ∏ÂÖã‰ø°ÊÅØ
						this.getImagesForInventory(); // Ëé∑ÂèñÂõæÁâá
						this.getRecipes(); // Ëé∑ÂèñÊé®ËçêÈ£üË∞±
					} else {
						console.error('Ëé∑ÂèñÂ∫ìÂ≠òÂ§±Ë¥•:', res.result.msg);
					}
				} catch (error) {
					console.error('Ëé∑ÂèñÂ∫ìÂ≠òÂ§±Ë¥•:', error);
					uni.showToast({
						title: 'Ëé∑ÂèñÂ∫ìÂ≠òÂ§±Ë¥•',
						icon: 'none'
					});
				}
			},
			async getIncompatibilityInfo() {
				try {
					// Ëé∑ÂèñÂ∫ìÂ≠ò‰∏≠ÊâÄÊúâÁâ©ÂìÅÁöÑÂêçÁß∞
					const itemNames = this.inventory.map(item => item.name);

					// Ë∞ÉÁî®‰∫ëÂáΩÊï∞Êü•ËØ¢Áõ∏ÂÖã‰ø°ÊÅØ
					const res = await uniCloud.callFunction({
						name: 'get-incompatibility-info',
						data: {
							itemNames: itemNames
						}
					});

					if (res.result.code === 0) {
						this.incompatibilityList = res.result.data;
					} else {
						console.error('Ëé∑ÂèñÁõ∏ÂÖã‰ø°ÊÅØÂ§±Ë¥•:', res.result.msg);
					}
				} catch (error) {
					console.error('Ëé∑ÂèñÁõ∏ÂÖã‰ø°ÊÅØÂ§±Ë¥•:', error);
				}
			},
			async getRecipes() {
				try {
					// Ëé∑ÂèñÂ∫ìÂ≠ò‰∏≠ÊâÄÊúâÁâ©ÂìÅÁöÑÂêçÁß∞
					const itemNames = this.inventory.map(item => item.name);

					// Ë∞ÉÁî®‰∫ëÂáΩÊï∞Êü•ËØ¢È£üË∞±
					const res = await uniCloud.callFunction({
						name: 'recommend-recipe',
						data: {
							inventory: itemNames
						}
					});

					if (res.result.code === 0) {
						this.recipes = res.result.data;
					} else {
						console.error('Ëé∑ÂèñÈ£üË∞±Â§±Ë¥•:', res.result.msg);
					}
				} catch (error) {
					console.error('Ëé∑ÂèñÈ£üË∞±Â§±Ë¥•:', error);
				}
			},
			async startVoiceInput() {
				try {
					const res = await uni.startRecord();
					console.log('ÂΩïÈü≥ÂºÄÂßã');
					setTimeout(async () => {
						const res = await uni.stopRecord();
						console.log('ÂΩïÈü≥ÁªìÊùü', res);
						const text = await this.recognizeSpeech(res.tempFilePath);
						const itemInfo = this.parseVoiceInput(text);
						await this.submitVoiceData(itemInfo);
					}, 5000); // ÂΩïÈü≥5Áßí
				} catch (error) {
					console.error('ÂΩïÈü≥Â§±Ë¥•:', error);
					uni.showToast({
						title: 'ÂΩïÈü≥Â§±Ë¥•',
						icon: 'none'
					});
				}
			},
			async recognizeSpeech(filePath) {
				// Ë∞ÉÁî®ËØ≠Èü≥ËØÜÂà´API
				// ‰ΩøÁî®uniCloudÁöÑËØ≠Èü≥ËØÜÂà´ÂäüËÉΩ
				const res = await uniCloud.callFunction({
					name: 'speech-recognition',
					data: {
						filePath: filePath
					}
				});
				return res.result.text;
			},
			parseVoiceInput(text) {
				// Ëß£ÊûêËØ≠Èü≥ËØÜÂà´ÁªìÊûú
				// ÂÅáËÆæËØ≠Èü≥‰∏∫ "ÁâõÂ•∂ 3 2023-12-31"
				const [name, quantity, expireDate] = text.split(' ');
				return {
					name: name,
					quantity: parseInt(quantity),
					expireDate: expireDate
				};
			},
			async submitVoiceData(itemInfo) {
				try {
					const res = await uniCloud.callFunction({
						name: 'submit-data',
						data: itemInfo
					});
					if (res.result.code === 0) {
						uni.showToast({
							title: 'ËØ≠Èü≥ÂΩïÂÖ•ÊàêÂäü',
							icon: 'success'
						});
						this.getInventory();
					}
				} catch (error) {
					console.error('ËØ≠Èü≥ÂΩïÂÖ•Â§±Ë¥•:', error);
					uni.showToast({
						title: 'ËØ≠Èü≥ÂΩïÂÖ•Â§±Ë¥•',
						icon: 'none'
					});
				}
			},
			async queryInventory() {
				if (!this.queryItemName) {
					uni.showToast({
						title: 'ËØ∑ËæìÂÖ•Ë¶ÅÊü•ËØ¢ÁöÑÁâ©ÂìÅÁßçÁ±ª',
						icon: 'none'
					});
					return;
				}

				try {
					// ÊòæÁ§∫Âä†ËΩΩÁä∂ÊÄÅ
					uni.showLoading({
						title: 'Êü•ËØ¢‰∏≠...'
					});

					// Ë∞ÉÁî®‰∫ëÂáΩÊï∞Êü•ËØ¢Â∫ìÂ≠ò
					const res = await uniCloud.callFunction({
						name: 'get-inventory',
						data: {
							itemName: this.queryItemName
						}
					});

					// ÈöêËóèÂä†ËΩΩÁä∂ÊÄÅ
					uni.hideLoading();

					if (res.result.code === 0) {
						this.inventory = res.result.data;
						if (this.inventory.length === 0) {
							uni.showToast({
								title: 'Êú™ÊâæÂà∞ËØ•Áâ©ÂìÅ',
								icon: 'none'
							});
						} else {
							// ÊòæÁ§∫Êü•ËØ¢ÁªìÊûú
							const item = this.inventory[0]; // Ëé∑ÂèñÁ¨¨‰∏Ä‰∏™Êü•ËØ¢ÁªìÊûú
							uni.showToast({
								title: `ÂêçÁß∞Ôºö${item.name}\nÊï∞ÈáèÔºö${item.quantity}\n‰øùË¥®ÊúüÔºö${item.expireDate}\nÊâ´ÊèèÊó•ÊúüÔºö${item.scanDate}`,
								icon: 'none',
								duration: 3000 // ÊòæÁ§∫3Áßí
							});
						}
					} else {
						uni.showToast({
							title: 'Êü•ËØ¢Â§±Ë¥•',
							icon: 'none'
						});
					}
				} catch (error) {
					console.error('Êü•ËØ¢Â§±Ë¥•:', error);
					uni.hideLoading();
					uni.showToast({
						title: 'Êü•ËØ¢Â§±Ë¥•',
						icon: 'none'
					});
				}
			},
			async getImagesForInventory() {
				try {
					// Ëé∑ÂèñÂ∫ìÂ≠ò‰∏≠ÊâÄÊúâÁâ©ÂìÅÁöÑÂêçÁß∞
					const itemNames = this.inventory.map(item => item.name);

					// Ê£ÄÊü•Âì™‰∫õÁâ©ÂìÅÁöÑÂõæÁâáÈúÄË¶ÅÊõ¥Êñ∞
					const itemsToUpdate = itemNames.filter(name => {
						const item = this.inventory.find(i => i.name === name);
						return !item.imageUrl || Date.now() - item.lastUpdated > 7 * 24 * 60 * 60 * 1000; // Ë∂ÖËøá7Â§©Êú™Êõ¥Êñ∞
					});

					if (itemsToUpdate.length > 0) {
						// Ë∞ÉÁî®‰∫ëÂáΩÊï∞Êü•ËØ¢ÂõæÁâá
						const res = await uniCloud.callFunction({
							name: 'get-item-images',
							data: {
								itemNames: itemsToUpdate
							}
						});

						if (res.result.code === 0) {
							// Êõ¥Êñ∞ inventory ‰∏≠ÁöÑÂõæÁâá URL
							this.inventory = this.inventory.map((item) => {
								const index = itemsToUpdate.indexOf(item.name);
								return {
									...item,
									imageUrl: index !== -1 ? res.result.data[index] : item.imageUrl
								};
							});
						} else {
							console.error('Ëé∑ÂèñÂõæÁâáÂ§±Ë¥•:', res.result.msg);
						}
					}
				} catch (error) {
					console.error('Ëé∑ÂèñÂõæÁâáÂ§±Ë¥•:', error);
				}
			},
			async scanItem() {
				try {
					// Ë∞ÉÁî®ÊëÑÂÉèÂ§¥Êâ´Êèè
					const res = await uni.chooseImage({
						count: 1, // Âè™ÈÄâÊã©‰∏ÄÂº†ÂõæÁâá
						sourceType: ['camera'], // ‰ªéÊëÑÂÉèÂ§¥Ëé∑Âèñ
						sizeType: ['compressed'] // ÂéãÁº©ÂõæÁâá
					});

					if (res.tempFilePaths.length > 0) {
						const tempFilePath = res.tempFilePaths[0];

						// ‰∏ä‰º†ÂõæÁâáÂà∞‰∫ëÁ´Ø
						const uploadRes = await uniCloud.uploadFile({
							filePath: tempFilePath,
							cloudPath: `scanned-images/${Date.now()}.jpg` // ‰∫ëÁ´ØÂ≠òÂÇ®Ë∑ØÂæÑ
						});

						if (uploadRes.fileID) {
							// Â∞ÜÂõæÁâá URL ‰øùÂ≠òÂà∞Êï∞ÊçÆÂ∫ì
							const dbRes = await uniCloud.callFunction({
								name: 'scan-item',
								data: {
									imageUrl: uploadRes.fileID // ‰ΩøÁî® fileID ‰Ωú‰∏∫ÂõæÁâá URL
								}
							});

							if (dbRes.result.code === 0) {
								uni.showToast({
									title: 'Êâ´ÊèèÊàêÂäüÔºåÂõæÁâáÂ∑≤‰øùÂ≠ò',
									icon: 'success'
								});
								this.getInventory(); // Âà∑Êñ∞Â∫ìÂ≠ò
							} else {
								uni.showToast({
									title: '‰øùÂ≠òÂõæÁâáÂ§±Ë¥•',
									icon: 'none'
								});
							}
						} else {
							uni.showToast({
								title: '‰∏ä‰º†ÂõæÁâáÂ§±Ë¥•',
								icon: 'none'
							});
						}
					}
				} catch (error) {
					console.error('Êâ´ÊèèÂ§±Ë¥•:', error);
					uni.showToast({
						title: 'Êâ´ÊèèÂ§±Ë¥•',
						icon: 'none'
					});
				}
			},
			navigateToManualInput() {
				uni.navigateTo({
					url: '/pages/manual-input/manual-input'
				});
			},
			async recommendRecipe() {
				// 1. Ê£ÄÊü•Áî®Êà∑ÊòØÂê¶ËæìÂÖ•‰∫ÜÈ£üÊùê
				if (!this.recipeQuery) {
					uni.showToast({
						title: 'ËØ∑ËæìÂÖ•È£üÊùê',
						icon: 'none'
					});
					return;
				}

				try {
					// ÊòæÁ§∫Âä†ËΩΩÁä∂ÊÄÅ
					uni.showLoading({
						title: 'Êü•ËØ¢‰∏≠...'
					});

					// 2. Ë∞ÉÁî®‰∫ëÂáΩÊï∞Êü•ËØ¢È£üË∞±
					const res = await uniCloud.callFunction({
						name: 'recommend-recipe',
						data: {
							inventory: [this.recipeQuery] // Â∞ÜÁî®Êà∑ËæìÂÖ•ÁöÑÈ£üÊùê‰Ωú‰∏∫Êü•ËØ¢Êù°‰ª∂
						}
					});

					// ÈöêËóèÂä†ËΩΩÁä∂ÊÄÅ
					uni.hideLoading();

					// 3. Ê£ÄÊü•‰∫ëÂáΩÊï∞ËøîÂõûÁöÑÁªìÊûú
					if (res.result.code === 0) {
						if (res.result.data && res.result.data.length > 0) {
							// ÊàêÂäüËé∑ÂèñÂà∞È£üË∞±
							this.recipes = res.result.data;
							uni.showToast({
								title: 'Êé®ËçêÊàêÂäü',
								icon: 'success'
							});
						} else {
							// Êú™ÊâæÂà∞Áõ∏ÂÖ≥È£üË∞±
							uni.showToast({
								title: 'Êú™ÊâæÂà∞Áõ∏ÂÖ≥È£üË∞±',
								icon: 'none'
							});
						}
					} else {
						// ‰∫ëÂáΩÊï∞ËøîÂõûÈîôËØØ
						uni.showToast({
							title: res.result.msg || 'Êé®ËçêÂ§±Ë¥•',
							icon: 'none'
						});
					}
				} catch (error) {
					// 4. ÊçïËé∑ÂºÇÂ∏∏Âπ∂Â§ÑÁêÜ
					console.error('Êé®ËçêÂ§±Ë¥•:', error);
					uni.hideLoading();
					uni.showToast({
						title: 'Êé®ËçêÂ§±Ë¥•ÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúËøûÊé•',
						icon: 'none'
					});
				}
			},
			navigateToManualRecipeInput() {
				uni.navigateTo({
					url: '/pages/manual-recipe-input/manual-recipe-input',
					success: () => {
						console.log('ÊàêÂäüË∑≥ËΩ¨Âà∞ÊâãÂä®ÂΩïÂÖ•ËèúË∞±È°µÈù¢');
					},
					fail: (err) => {
						console.error('ÂØºËà™Â§±Ë¥•:', err);
						uni.showToast({
							title: 'Êó†Ê≥ïÊâìÂºÄÈ°µÈù¢ÔºåËØ∑Á®çÂêéÈáçËØï',
							icon: 'none'
						});
					}
				});
			}
		}
	}
</script>

